<template>
  <div>

<nav-bar :name="title"></nav-bar>

        <v-container class="text-center">
        <router-link to="/addstore">
          <v-btn color="#E47911" large height="40px" dark>
            <v-icon
              left
              dark
              > mdi-plus 
            </v-icon>  Add a new store
          </v-btn>
        </router-link>
        </v-container>
          <v-card class="ma-auto pb-5" width="90%" outlined>
            <v-container class="text-center">
                <v-card>
                  <v-layout>
            <v-col cols="10" class="text-right">
              <v-btn
                class="mx-auto"
                icon
                @click="activeExport"
              >
                <v-icon size="24px" :color="export_btn_color">
                  mdi-file-move
                </v-icon>
              </v-btn>
            </v-col>
            <v-col cols="2" class="text-right">
              <v-btn
                class="mx-auto"
                icon
                @click="activeDelete"
              >
                <v-icon size="24px" :color="delete_btn_color">
                  mdi-delete
                </v-icon>
              </v-btn>
            </v-col>
            </v-layout>
                    <v-card-title>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :search="search"
                      :items="stores"
                      :loading="loading"
                      loading-text="Loading... Please wait"
                      hide-default-footer
                      :page.sync="page"
                      :items-per-page="itemsPerPage"
                      @page-count="pageCount = $event"
                      >
                      <template v-slot:item.name="{ item }">
                          {{ item.name  }}  |  
                          <router-link :to="{ name: 'ViewStore', params: { 'id': item.unique_id }}">
                          View Store <v-icon class="ml-1" color="#E47911" size="16px"> mdi-eye </v-icon>
                        </router-link>
                      </template>
                      <template v-slot:item.status="{ item }">
                        <v-chip
                          :color="getColor(item.status)"
                          dark
                        >
                          {{ item.status | name_status }}
                        </v-chip>
                      </template>
                      <template v-slot:item.unique_id="{ item }">
                        <v-menu
                          top
                          offset-x
                          v-if="select"
                        >
                          <template v-slot:activator="{ on, attrs }">
                          <v-btn icon
                            dark
                            v-bind="attrs"
                            v-on="on"
                          >
                              <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                          </v-btn>
                          </template>
                          <v-list>
                          <v-list-item link>
                            <v-list-item-title>
                              <router-link :to="{ name: 'UpdateStore', params: { 'id': item.unique_id }}"> Update store </router-link>
                            </v-list-item-title>
                          </v-list-item>
                          <v-list-item link>
                            <v-list-item-title>
                              <router-link :to="{ name: 'AddWalmart', params: { 'id': item.unique_id }}"> Add walmart info to store </router-link>
                            </v-list-item-title>
                          </v-list-item>
                          <v-list-item @click="delete_store(item.unique_id)" link>
                            <v-list-item-title> Delete register </v-list-item-title>
                          </v-list-item>
                          </v-list>
                        </v-menu>
                            <v-checkbox
                                :value="item.unique_id"
                                color="#E47911"
                                v-model="idStore"
                                v-else
                                class="my-n3"
                            ></v-checkbox>
                      </template>
                    </v-data-table>
                  </v-card>
                  <br>
                    <v-pagination
                      v-model="page"
                      :length="pageCount"
                    ></v-pagination>
     
        <v-row v-if="!select">
          <v-col>
          <v-btn
          class="mx-4 my-2"
          color="#989898"
          dark
          @click="desactivarBtn"
          >
          CANCEL
          </v-btn>
          <v-btn
          class="mx-4 my-2"
          color="#E47911"
          dark
          v-if="change"
          @click="export_stores"
          :loading="loading_exp"
          >
          EXPORT
          </v-btn>
          <v-btn
          class="mx-4 my-2"
          color="#E47911"
          dark
          v-if="!change"
          @click="delete_stores"
          :loading="loading_del"
          >
          DELETE
          </v-btn>
          </v-col>
        </v-row>

        </v-container>
          </v-card>

<footer-nav></footer-nav>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import StoreService from '@/services/StoreService.js';
  import Swal from 'sweetalert2'

  export default {
    components: {
      NavBar, FooterNav
    },
    data: () => ({
      title: 'STORES',
      select: true,
      change: true,
      loading: true,
      loading_exp: false,
      loading_del: false,
      export_btn_color: '#000',
      delete_btn_color: '#000',
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      search: '',
      stores: [

      ],
      idStore: [],
      headers: [
        {
          text: 'Store',
          value: 'name'
        },
        { 
          text: 'Status',
          value: 'status'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
    }),

    mounted() {
      StoreService
        .list_admin()
        .then(res => {
          this.stores = res;
          this.loading = false
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
          });
              this.loading = false;
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
    },

    filters:{
      name_status(value){
        switch (value) {
          case 'A': return 'Active'
            break;
          
          case 'ED': return 'Everyday'
            break;

          case 'S': return 'Suspended'
            break;
          
          case 'V': return 'Vacation'
            break;

          case 'DA': return 'Denied activation'
            break;
        
          default: return 'Pending activation'
            break;
        }
      }
    },

    methods: {
      activeExport(){
        this.change = true;
          this.select = !this.select;
          this.export_btn_color = this.select ? '#000' : '#E47911';
          this.delete_btn_color = '#000';
      },

      activeDelete(){
        this.change = false;
        this.select = !this.select;
        this.delete_btn_color = this.select ? '#000' : '#E47911';
        this.export_btn_color = '#000';
      },

      desactivarBtn(){
        this.select = !this.select;
        this.export_btn_color = '#000';
        this.delete_btn_color = '#000'
      },

      getColor (status) {
        if (status == "A") return 'green'
        else if (status == 'ED') return 'purple'
        else if (status == 'S') return 'red'
        else if (status == 'DA') return 'gray'
        else if (status == 'V') return 'yellow'
        else return 'orange'
      },

      refresh_list(){
        StoreService
          .list_admin()
          .then(res => {
            this.stores = res;
            this.loading = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      },

      delete_stores(){
        this.loading_del = true;
        StoreService
          .delete_stores(this.idStore)
          .then(res => {
            this.loading_del = false
            Swal.fire({
            icon: 'success',
            title: 'Success',
            text: res['message'],
            }).then(() => {
              this.refresh_list();
            })
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      },

      export_stores(){
        this.loading_exp = true;
        StoreService
          .export_stores(this.idStore)
          .then(res => {
              const url = window.URL.createObjectURL(new Blob([res.data]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'stores.xlsx'); //or any other extension
              document.body.appendChild(link);
              link.click();
            this.loading_exp = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
      },

      delete_store(pk){
        Swal.fire({
          icon: 'question',
          title: 'Confirm',
          text: 'Are you sure you want to delete this store?',
          confirmButtonText: 'YES',
          cancelButtonText: 'NO',
          showCancelButton: true
        }).then(res => {
          if(res.isConfirmed){
            StoreService
            .delete(pk)
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Store successfully removed.'
            }).then(() => {
                this.refresh_list();
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
                msg+= key + ': ' + value + ' \n ';
            });
                this.loading = false;
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: msg
                });
            })
          }
        })
      }
  },

  }
</script>